<template>
    <div>
        <loading :active.sync="isLoading" ></loading>
        <div>
            <ul class="nav justify-content-around py-5 text-center">
                <li class="nav-item p-3 text-secondary rounded-pill w-25">填寫訂單資料</li>
                <li class="nav-item p-3 bg-desert text-light rounded-pill w-25" v-if="order.is_paid === false">確認資料及付款</li>
                <li class="nav-item p-3 text-secondary rounded-pill w-25" v-else>確認資料及付款</li>
                <li class="nav-item p-3 text-secondary rounded-pill w-25" v-if="order.is_paid === false">完成</li>
                <li class="nav-item p-3 bg-desert text-light rounded-pill w-25" v-if="order.is_paid === true">完成</li>
            </ul>
        </div>
        <div class="my-4 row justify-content-center">
            <form class="col-md-6" @submit.prevent="payOrder">
                <table class="table">
                <thead>
                    <th>品名</th>
                    <th>數量</th>
                    <th>單價</th>
                </thead>
                <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total | currency}}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="2" class="text-right">總計</td>
                    <td class="text-right"><strong>{{ order.total |currency}}</strong></td>
                    </tr>
                </tfoot>
                </table>

                <table class="table">
                <tbody>
                    <tr>
                    <th width="100">Email</th>
                    <td>{{ order.user.email }}</td>
                    </tr>
                    <tr>
                    <th>姓名</th>
                    <td>{{ order.user.name }}</td>
                    </tr>
                    <tr>
                    <th>收件人電話</th>
                    <td>{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                    <th>收件人地址</th>
                    <td>{{ order.user.address }}</td>
                    </tr>
                    <tr>
                    <th>付款狀態</th>
                    <td>
                        <span v-if="!order.is_paid" class="text-secondary">尚未付款</span>
                        <span v-else class="text-success">付款完成</span>
                    </td>
                    </tr>
                </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                <button class="btn btn-danger">確認付款去</button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            order: {
                user:{

                }
            },
            orderId: '',
            isLoading: false,
        }
    },
    methods:{
        getOrder(){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`
            vm.isLoading = true;
            vm.$http.get(api).then((response) => {
                console.log(response.data)
                vm.isLoading = false;
                vm.order = response.data.order;
            })
        },
        payOrder(){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`
            vm.$http.post(api).then((response) => {
                console.log(response.data)
                if(response.data.success){
                    vm.getOrder();
                }
            })
        }
    },
    created(){
        this.orderId = this.$route.params.orderId
        this.getOrder();
        console.log(this.orderId);
    }
}
</script>